h1 {
    color: white;
    padding: 0;
    margin: 0;
    margin-left: 1rem;
}

.material-list {
    column-count: 2;
}

.material {
    height: 3rem;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
}

.material-image {
    width: 2rem;
    height: 2rem;
    margin-right: 1rem;
}

.material-text {
    color: white;
}

/* fixes a bug with column-count in chrome */
li {
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid;
  }